import { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { deleteBoard, readBoard } from "../../service/boardService";
import './PostDoc.css'
import "react-quill/dist/quill.snow.css"
import styles from './PostContent.module.css'

const PostDoc = ({ value, isLogined })=>{

    const { postId } = useParams();
    const [ post, setPost ] = useState({})
    const navigate = useNavigate()
    const sectionTitle = ()=>{
        if(value === 'cases'){
            return ''
        }
        if(value === 'guide'){
            return ''
        }
        if(value === 'video'){
            return ''
        }
    }
    useEffect(()=>{
        const fetchPosts = async () => {
            const res = await readBoard({ value, postId })
            setPost(res.data);
        }
        fetchPosts();
    },[value, postId])

    const deletePost = ()=>{
        if (window.confirm("  ?")) {
            deleteBoard({value, postId})
            .then(response => {
                if(response.data.message === "Success") {
                    alert(' ')
                    navigate(`/${ value }`)
                }
            }).catch(err=>alert(`Error : ${ err.message }`))
        } else {
        }
    }
    return(
        <div className="PostDoc">
            <div className="Section01">
                <p className="sectionTitle">{ sectionTitle() }</p>
                <p className="categoryTitle">{post.category}</p>
            </div>
            <div className="Section02">         
                <h2>{post.title}</h2>
                <p className="editor"> : </p>
                <div className="editDate">
                    <p className="createdAt"> : {post.createdAt}</p>
                    {
                        !post.updatedAt
                        ? null
                        : <p className="createdAt"> : {post.updatedAt}</p>
                    }
                    <p className="createdAt"> : {post.views}</p>
                </div>
            </div>
            {
                isLogined === true
                ? <div className="SectionAdmin">
                    <button onClick={()=>{
                        navigate(`/${ value }/rewrite/${ postId }`)
                    }}></button>
                    <button onClick={ deletePost } ></button>
                </div>
                : null
            }
            <div className="Section03">
                <div className={ styles.PostDocContents } dangerouslySetInnerHTML={{ __html : post.content }}/>
            </div>
        </div>
    )
}
export default PostDoc