import EditorComponent from "../../components/EditorComponent/EditorComponent"
import { useEffect, useState } from "react"
import { useNavigate } from 'react-router-dom';
import { createCounsel } from "../../service/counselService";
import './CounselEditor.css'

const CounselEditor = ()=>{
    const navigate = useNavigate()
    const [ input, setInput ] = useState({
        category : "",
        title : '',
        name : '',
        phone : '',
        password : ''
    })
    const { category, title, name, phone, password } = input
    const [content, setContent] = useState("")

    const handleInput = (e)=>{
        setInput({
            ...input,
            [ e.target.name ] : e.target.value
        })
    }

    const sendBtn = ()=>{
        if(!title || !name || !phone || password.length !== 4 || !content){
            return alert('  .')
        }
        createCounsel( {
            category : category,
            title : title,
            name : name,
            phone : phone,
            password : password,
            content : content,
        }).then(response => {
            if(response.data.message === "Success") {
                alert('  ')
                navigate('/counsel')
            }
        }).catch(err=>alert(`Error : ${ err.message }`))
    }

    const cancleBtn = ()=>{
        if (window.confirm(" ?")) {
            navigate(-1)
          } else {
          }
    }
    useEffect(()=>{
        window.scrollTo({ top: 0 });
    }, [])
    return (
        <div className="CounselEditor">
            <div className="container">
                <h2> </h2>
                <label htmlFor="title"></label>
                <input className="titleInput" type="text" placeholder=" " onChange={ handleInput } name="title"/>
                <label htmlFor="title"></label>
                <input className="titleInput" type="text" placeholder="" onChange={ handleInput } name="name"/>
                <label htmlFor="title"></label>
                <input className="titleInput" type="text" placeholder="" onChange={ handleInput } name="phone"/>
                <label htmlFor="title"></label>
                <input className="titleInput" type="text" placeholder="  4 " maxLength={ 4 } onChange={ handleInput } name="password"/>
                <label htmlFor="title"></label>
                <select onChange={ handleInput } name="category">
                <option value=" "> </option>
                    <option value=" "> </option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value="/">/</option>
                    <option value="/">/</option>
                    <option value="/">/</option>
                    <option value=" "> </option>
                    <option value="/">/</option>
                    <option value="//">//</option>
                    <option value="//">//</option>
                </select>
                <div className='EditorComponent'>
                    <EditorComponent  content={content} setContent={setContent}/>
                </div>
                <div className="BtnWrap">
                    <button className="Btn" onClick={ sendBtn }></button>
                    <button className="Btn" onClick={ cancleBtn }></button>
                </div>
            </div>
        </div>
    )
}
export default CounselEditor