import EditorComponent from "../../components/EditorComponent/EditorComponent"
import { useEffect, useState } from "react"
import { useLocation, useNavigate, useParams } from 'react-router-dom';
import axios from 'axios'

const CaseRewrite = ()=>{
    const navigate = useNavigate()
    const { postId } = useParams()
    const { state } = useLocation()
    const [ category, setCategory ] = useState("")
    const [ title, setTitle ] = useState("")
    const [ content, setContent ] = useState("")
    const [ post, setPost ] = useState({})
    const handleTitle = (e)=>{
        setTitle(e.target.value)
    }
    const handleCategory = (e)=>{
        setCategory(e.target.value)
    }
    useEffect(()=>{
        const fetchPost = async () => {
            const res = await axios.get(`http://localhost:8080/api/cases/${postId}`)
            setPost(res.data)
            setTitle(res.data.title)
            setContent(res.data.content)
            setCategory(res.data.category)
        }
        fetchPost();
    }, [])
    const sendBtn = ()=>{
        axios.put(`http://localhost:8080/api/cases/${postId}`, {
            category : category,
            title : title,
            content : content
        }).then(response => {
            if(response.data.message === "updated") {
                alert(' ')
                navigate('/cases')
            }
        }).catch(err=>alert(`Error : ${ err.message }`))
    }
    const cancleBtn = ()=>{
        if (window.confirm(" ?")) {
            navigate('/')
          } else {
          }
    }
    
    return (
        <div className="CasesEditor">
            <div className="container">
                <h2> </h2>
                <label htmlFor="category"></label>
                <select onChange={ handleCategory } name="category" defaultValue={ category }>
                    <option value=""></option>
                    <option value="/">/</option>
                    <option value=""></option>
                    <option value="/">/</option>
                    <option value="/">/</option>
                    <option value=""></option>
                    <option value="/">/</option>
                    <option value=" "> </option>
                    <option value=" "> </option>
                    <option value="//">//</option>
                    <option value="//">//</option>
                    <option value=""></option>
                </select>
                <label htmlFor="title"></label>
                <input className="titleInput" type="text" placeholder=" " onChange={ handleTitle } name="title" defaultValue={ title }/>
                <label htmlFor=""></label>
                <div className='EditorComponent'>
                    <EditorComponent  content={content} setContent={setContent} defaultValue={ content }/>
                </div>
                <div className="BtnWrap">
                    <button className="Btn" onClick={ sendBtn }></button>
                    <button className="Btn" onClick={ cancleBtn }></button>
                </div>
            </div>
        </div>
    )
}
export default CaseRewrite