import { useNavigate } from "react-router-dom"
import './Menu.css'

const Menu =()=>{
    const navigate = useNavigate();
    const handleMenu = (e)=>{
        navigate(`/${e.target.value}`)
    }
    return(
        <div className="Menu">
            <button onClick={ handleMenu } value='cases'></button>
            <button onClick={ handleMenu } value='guide'></button>
            <button onClick={ handleMenu } value='video'></button>
        </div>
    )
}
export default Menu