import { useDispatch } from 'react-redux'
import { logout } from '../../redux/user';
import './Logout.css'

const Logout = ()=>{
    const dispatch = useDispatch()
    const LogoutBtn = ()=>{
        dispatch(logout())
    }
    return(
        <div className='Logout'>
            <h2> </h2>
            <button onClick={ LogoutBtn }></button>
        </div>
    )
}
export default Logout