import EditorComponent from "../../components/EditorComponent/EditorComponent"
import { useEffect, useState } from "react"
import { useLocation, useNavigate, useParams } from 'react-router-dom';
import './CounselEditor.css'
import { readCounsel, updateCounsel } from "../../service/counselService";

const CounselRewrite = ()=>{
    const { postId } = useParams();
    const { state } = useLocation();

    const navigate = useNavigate()
    const [ input, setInput ] = useState({
        category : "",
        title : '',
        name : '',
        phone : '',
        password : ''
    })

    const { category, title, name, phone, password } = input
    const [content, setContent] = useState("")

    const handleInput = (e)=>{
        setInput({
            ...input,
            [ e.target.name ] : e.target.value
        })
    }
    useEffect(()=>{
        if(!state){
            return alert('    .')
        } else{
            const fetchPost = async () => {
                
                const res = await readCounsel({ postId })
                setInput({
                    ...input,
                    category : res.data.category,
                    title : res.data.title,
                    name : res.data.name,
                    phone : res.data.phone,
                    password : res.data.password
                })
                setContent(res.data.content)
    
            }
            fetchPost();
        }
    }, [])

    const sendBtn = ()=>{
        updateCounsel({
            postId : postId,
            category : category,
            title : title,
            name : name,
            phone : phone,
            password : password,
            content : content,
        }).then(response => {
            if(response.data.message === "updated") {
                alert(' ')
                navigate('/counsel')
            }
        }).catch(err=>alert(`Error : ${ err.message }`))
    }

    const cancleBtn = ()=>{
        if (window.confirm(" ?")) {
            navigate(-1)
          } else {
          }
    }
    
    return (
        <div className="CounselEditor">
            <div className="container">
                <h2> </h2>
                <label htmlFor="title"></label>
                <input className="titleInput" defaultValue={ title } type="text" placeholder=" " onChange={ handleInput } name="title"/>
                <label htmlFor="title"></label>
                <input className="titleInput" defaultValue={ name } type="text" placeholder="" onChange={ handleInput } name="name"/>
                <label htmlFor="title"></label>
                <input className="titleInput" defaultValue={ phone } type="text" placeholder="" onChange={ handleInput } name="phone"/>
                <label htmlFor="title"></label>
                <input className="titleInput" defaultValue={ password } type="text" placeholder="" onChange={ handleInput } name="password"/>
                <label htmlFor="title"></label>
                <select onChange={ handleInput } name="category" defaultValue={ category }>
                    <option value=""></option>
                    <option value="/">/</option>
                    <option value=""></option>
                    <option value="/">/</option>
                    <option value="/">/</option>
                    <option value=""></option>
                    <option value="/">/</option>
                    <option value=" "> </option>
                    <option value=" "> </option>
                    <option value="//">//</option>
                    <option value="//">//</option>
                    <option value=""></option>
                </select>
                <div className='EditorComponent'>
                    <EditorComponent content={content} setContent={setContent} defaultValue={ content }/>
                </div>
                <div className="BtnWrap">
                <button className="Btn" onClick={ sendBtn }></button>
                    <button className="Btn" onClick={ cancleBtn }></button>
                </div>
            </div>
        </div>
    )
}
export default CounselRewrite