import { useEffect, useState } from "react"
import { useNavigate, useParams } from 'react-router-dom';
import { getVideo, updateVideo } from "../../service/videoService";

const VideoRewrite = ()=>{
    const navigate = useNavigate()
    const { videoId } = useParams();
    const [ title, setTitle ] = useState("")
    const [ url, setUrl ] = useState("")
    const [ keyword, setKeyword ] = useState("")

    useEffect(()=>{
        const fetchVideo = async () => {
            const res = await getVideo({ videoId })
            setTitle(res.data.title)
            setUrl(`https://www.youtube.com/watch?v=${ res.data.url }`)
            setKeyword(res.data.keyword)
        }
        fetchVideo();
    }, [videoId])

    const handleTitle = (e)=>{
        setTitle(e.target.value)
    }
    const handleURL = (e)=>{
        setUrl(e.target.value)
    }
    const handleKeyword = (e)=>{
        setKeyword(e.target.value)
    }
    const sendBtn = ()=>{
        updateVideo({ 
            videoId : videoId,
            title : title,
            url : url.replace('https://www.youtube.com/watch?v=', ''),
            keyword : keyword
        }).then(response => {
            if(response.data.message === "updated") {
                alert(' ')
                navigate('/video')
            }
        }).catch(err=>alert(`Error : ${ err.message }`))
    }

    const cancleBtn = ()=>{
        if (window.confirm(" ?")) {
            navigate('/video')
          } else {
          }
    }


    return (
        <div className="CasesEditor">
            <div className="container">
                <h2> </h2>
                <label htmlFor="title"></label>
                <input className="titleInput" type="text" placeholder=" " name="title" onChange={ handleTitle } defaultValue={ title }/>
                <label htmlFor="url"> URL </label>
                <input className="titleInput" type="text" placeholder="Youtube URL " name="url" onChange={ handleURL } defaultValue={ url }/>
                <label htmlFor="keyword"></label>
                <input className="titleInput" type="text" placeholder=" " name="keyword" onChange={ handleKeyword } defaultValue={ keyword }/>
                <div className="BtnWrap">
                    <button className="Btn" onClick={ sendBtn }></button>
                    <button className="Btn" onClick={ cancleBtn }></button>
                </div>
            </div>
        </div>
    )
}
export default VideoRewrite