import './Profile.css'
import ProfileImg from '../../assets/images/ProfileImg.png'

const Profile = ()=>{
    return(
        <div className='Profile'>
            <div>
                <div>
                    <p className='ProfileText01'> </p>
                    <p className='ProfileText02'><span className='orange'>.</span></p>
                </div>
                <div className='ProfileText03'>
                    <p>  </p>
                </div>
                <a className='ProfileBtn' href="/counsel/write"></a>
            </div>
            <div>
                <div className='ProfileImg'>
                    <img alt="ProfileImg" src={ ProfileImg } />
                </div>
            </div>
        </div>
    )
}
export default Profile;