import EditorComponent from "../../components/EditorComponent/EditorComponent"
import { useState } from "react"
import { useNavigate } from 'react-router-dom';
import axios from 'axios'
import './CasesEditor.css'

const CasesEditor = ()=>{
    const navigate = useNavigate()
    const [ title, setTitle ] = useState("")
    const [ content, setContent ] = useState("")
    const [ category, setCategory ] = useState("")

    const handleTitle = (e)=>{
        setTitle(e.target.value)
    }
    const handleCategory = (e)=>{
        setCategory(e.target.value)
    }

    const sendBtn = ()=>{
        if(!title || !content ){
            return alert(' ')
        }
        axios.post('http://localhost:8080/api/cases', {
            category : category,
            title : title,
            content : content
        }).then(response => {
            if(response.data.message === "Success") {
                alert(' ')
                navigate('/')
            }
        }).catch(err=>alert(`Error : ${ err.message }`))
    }
    const cancleBtn = ()=>{
        if (window.confirm(" ?")) {
            navigate('/cases')
          } else {
          }
    }
    
    return (
        <div className="CasesEditor">
            <div className="container">
                <h2> </h2>
                <label htmlFor="category"></label>
                <select onChange={ handleCategory } name="category">
                    <option value=" "> </option>
                    <option value=" "> </option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value="/">/</option>
                    <option value="/">/</option>
                    <option value="/">/</option>
                    <option value=" "> </option>
                    <option value="/">/</option>
                    <option value="//">//</option>
                    <option value="//">//</option>
                </select>
                <label htmlFor="title"></label>
                <input className="titleInput" type="text" placeholder=" " onChange={ handleTitle } name="title"/>
                <label htmlFor=""></label>
                <div className='EditorComponent'>
                    <EditorComponent  content={content} setContent={setContent}/>
                </div>
                <div className="BtnWrap">
                    <button className="Btn" onClick={ sendBtn }></button>
                    <button className="Btn" onClick={ cancleBtn }></button>
                </div>
            </div>
        </div>
    )
}
export default CasesEditor