import { useEffect, useState } from "react";
import './Introduce.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronDown, faChevronUp } from '@fortawesome/free-solid-svg-icons'

const Introduce =()=>{
    const [ status, setStatus ] = useState('')
    const [ news, setNews ] = useState([{
      company : '',
      createdAt : ' 2022.08.24',
      title : '       ?',
      url : 'https://post.naver.com/viewer/postView.naver?volumeNo=34363166&memberNo=47450257'
    }, {
      company : '',
      createdAt : ' 2022.03.08',
      title : '     .',
      url : 'https://lawtalknews.co.kr/article/7XNEMIBOB9LO'
    },{
      company : 'JTBC',
      createdAt : ' 2021.08.15',
      title : '[]    ? .',
      url : 'https://news.nate.com/view/20210815n14025'
    },{
      company : '',
      createdAt : ' 2021.02.18',
      title : '57        .',
      url : 'https://www.ltn.kr/news/articleView.html?idxno=30914'
    },
  ])
    const [ records, setRecords ] = useState([{
      years : '2008',
      contents : '   '
    },{
      years : '2015',
      contents : '(KAIST)   '
    },{
      years : '2015',
      contents : ' 53 '
    },{
      years : '2019',
      contents : '  '
    },{
      years : '2020',
      contents : ' '
    },{
      years : '2013',
      contents : ' '
    },{
      years : '2015-2015',
      contents : ' '
    },{
      years : '2014',
      contents : ' '
    },{
      years : '2014-2019',
      contents : ' '
    },{
      years : '2019',
      contents : '  '
    },{
      years : '2019',
      contents : ' '
    },{
      years : '2015-2019',
      contents : ' '
    }])
    return(
      <div className="Introduce">
        <div className="ControlBar" >
          <div onClick={()=>{
            setStatus('')
          }} className=          {
            status === ''
            ? 'ControlNews Active'
            : 'ControlNews'
          }>
            <p></p>
          </div>
          <div onClick={()=>{
            setStatus('')
          }} className= {
            status === ''
            ? 'ControlRecord Active'
            : 'ControlRecord'
          }>
            <p>  </p>
          </div>
        </div>
        <div className="Contents">
          {
            status === ''
            ? <News news={ news } />            
            : <Record records={ records }/>
          }
        </div>
      </div>
    )
}
const News =({ news })=>{
  return(
    <div className="newsList">
      {
        !news
        ? null
        : <>
        {
          news.map((a, i)=>{
            return(
              <div className="newsList" key={ i }>
                <div className="newsHead">
                  <p>{ a.company }</p>
                  <p>{ a.createdAt }</p>
                </div>
                <a href={ a.url } className="newsBody">{ a.title }</a>
              </div>
            )
          })
        }
        </>
      }
    </div>
  )
}
const Record =({ records })=>{
  const [ more, setMore ] = useState(false)
  const [ filtered, setFiltered ] = useState(records.slice(0, 5))

  const handleMore = ()=>{
    setMore(!more)
  }
  useEffect(()=>{
    if(more){
      setFiltered(records)
    } else {
      setFiltered(records.slice(0, 5))
    }
  }, [more])

  return(
    <div className="recordLists">
      {
        !filtered
        ? null
        : <>
        {
          filtered.map((a, i)=>{
            return(
              <div className="recordList">
                <p className="recordYear">{ a.years } -</p><p className="recordContent">{ a.contents }</p>
              </div>
            )
          })
        }
        </>
      }
      {
        !more
        ? <p className="moreBtn" onClick={ handleMore }>  <FontAwesomeIcon icon={ faChevronDown } /></p>
        : <p className="closeBtn" onClick={ handleMore }> <FontAwesomeIcon icon={ faChevronUp } /></p>
      }
    </div>
  )
}
export default Introduce;