import { useState } from "react"
import { useNavigate } from 'react-router-dom';
import { createVideo } from "../../service/videoService";

const VideoEditor = ()=>{
    const navigate = useNavigate()
    const [ title, setTitle ] = useState("")
    const [ url, setUrl ] = useState("")
    const [ keyword, setKeyword ] = useState("")

    const handleTitle = (e)=>{
        setTitle(e.target.value)
    }
    const handleURL = (e)=>{
        setUrl(e.target.value)
    }
    const handleKeyword = (e)=>{
        setKeyword(e.target.value)
    }

    const sendBtn = ()=>{
        if(!title || !url ){
            return alert(' ')
        }
        createVideo({
            title : title,
            url : url.replace('https://www.youtube.com/watch?v=', ''),
            keyword : keyword
        }).then(response => {
            if(response.data.message === "Success") {
                alert(' ')
                navigate('/video')
            }
        }).catch(err=>alert(`Error : ${ err.message }`))
    }
    const cancleBtn = ()=>{
        if (window.confirm(" ?")) {
            navigate('/video')
          } else {
          }
    }
    
    return (
        <div className="CasesEditor">
            <div className="container">
                <h2> </h2>
                <label htmlFor="title"></label>
                <input className="titleInput" type="text" placeholder=" " name="title" onChange={ handleTitle }/>
                <label htmlFor="url"> URL </label>
                <input className="titleInput" type="text" placeholder="Youtube URL " name="url" onChange={ handleURL }/>
                <label htmlFor="keyword"></label>
                <input className="titleInput" type="text" placeholder=" " name="keyword" onChange={ handleKeyword }/>
                <div className="BtnWrap">
                    <button className="Btn" onClick={ sendBtn }></button>
                    <button className="Btn" onClick={ cancleBtn }></button>
                </div>
            </div>
        </div>
    )
}
export default VideoEditor